body {
  font-family: var(--main-font);
  letter-spacing: .03em;
  color: var(--main-font-color);
  background-color: #9dc0bb;
  margin: 0;
  font-size: 14px;
}

.search-form {
  height: 40px;
  background-color: #dada90;
  justify-content: center;
  gap: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.search-form > input {
  width: 250px;
  font-size: 18px;
}

.gallery {
  flex-wrap: wrap;
  gap: 30px;
  padding-left: 8px;
  padding-right: 8px;
  display: flex;
}

.photo-card {
  max-width: 300px;
  background-color: bisque;
  border-radius: 5px;
  overflow: hidden;
}

.info {
  display: flex;
}

.gallery img {
  width: 100%;
  object-fit: cover;
  display: block;
}

.info-item {
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  display: flex;
}

button {
  width: 120px;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  outline: none;
  padding: 6px 8px;
}

